import React from 'react'
import styled from 'styled-components'

import { PageTitle, Section, Container, Title, SubTitle, Hr, Br } from '../../shared/styles'

const Flex___Evenly_Distributed_Children = styled.ul`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;

  li {
    list-style-type: none;
    margin: 0.5em 0;
    transition: opacity 0.2s;
  }

  &:hover li:not(:hover) {
    opacity: 0.3;
  }

  @media (min-width: 768px) {
    flex-direction: row;
    justify-content: space-between;
  }
`

const Flex___Centering = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  background: lightgrey;
  height: 200px;

  div {
    background: red;
    width: 50px;
    height: 50px;
  }
`

export default () => {
  return (
    <div>
      <PageTitle>Playground</PageTitle>
      <Section>
        <Container>
          <Title>CSS Flex</Title>
          <SubTitle>Evenly distributed children</SubTitle>
          <Flex___Evenly_Distributed_Children>
            <li>Item1</li>
            <li>Item2</li>
            <li>Item3</li>
          </Flex___Evenly_Distributed_Children>
          <Hr />
          <Br />

          <SubTitle>Centering</SubTitle>
          <Flex___Centering>
            <div></div>
          </Flex___Centering>
          <Hr />
          <Br />

        </Container>
      </Section>
    </div>
  )
}



